@import url(https://fonts.googleapis.com/css2?family=Sansita+Swashed&display=swap);
.movies {
  display: -webkit-flex;
  display: flex;
  margin-top: 100px;
  -webkit-justify-content : space-around;
          justify-content : space-around;	
  -webkit-flex-wrap : wrap;	
          flex-wrap : wrap;
  margin-left: 50px;
  margin-right: 50px;
}

.movie{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap : wrap;
          flex-wrap : wrap;
  -webkit-flex-direction : column;
          flex-direction : column;	
  
}

.movie_rank{
  position: relative;
  color: white;
  font-size: 30px;
  top: 40px;
  left: 15px;
  font-family: 'Sansita Swashed', cursive;
  text-shadow: 0 4px 6px rgba(19, 19, 0, 0.1), 2px 5px 3px rgba(0, 0, 0, 0.8);
}

.movie_link{
  text-decoration : none; 
}

.movie_main_title{
  color: rgba(255, 255, 255, 0.938);
  font-size: 13px;
  text-align : center;
}

.movie_title{
  font-size: 17px;
  line-height : 10px;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #353232;
  height: 100%;
}

html,
body,
#root,
.App {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading {
  width: 100%;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-weight: 300;
  color: white;
}



::-webkit-scrollbar {
    width: 8px;
    background-color: #f1f1f1;
  }

  ::-webkit-scrollbar-thumb {
    background-color: #353b48;
  }

.movie_data{
    display: -webkit-flex;
    display: flex;
    margin-top: 140px;
    width: 80%;
    background-color: white;
    margin-bottom: 70px;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movie_data_info{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction : column;
            flex-direction : column;	
}
.movie_data img {
    position: relative;
    top: -50px;
    width: 200px;
    height: 300px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
      0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie_data_title{
    margin-top: 20px;
    height: 50px;
    margin-bottom: 10px;

}

.data_box{	
    padding: 10px;
    display: -webkit-flex;
    display: flex;
    border-top:  1px solid gray;;
  
}

.data_box ul{
    position: relative;
    list-style : none;
    left: -30px;
    top:-13px;
    font-size: 13px;
}

.movie_data_summary{
    overflow-y: scroll;
}

html {
    scroll-behavior: smooth;
}
:root {
    --topmenu-shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 3px rgba(0, 0, 0, 0.8);
    --shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 1px rgba(0, 0, 0, 0.8);
    --border: 1px solid rgba(0, 0, 0, 0.2);
    --menu-background-color: #fff;
    --menu-hover-background-color: #c1c1c1;
  }


.nav{
position:absolute;
width: 100%;
height: 60px;
-webkit-align-items : center;
        align-items : center;	
display: -webkit-flex;	
display: flex;
-webkit-justify-content : center;
        justify-content : center;	
-webkit-justify-content: space-evenly;	
        justify-content: space-evenly;	
background-color: #212121;
box-shadow: 0 4px 6px rgba(50, 50, 3, 0.1), 0 1px 3px rgba(0, 0, 0, 0.8);
box-shadow: var(--topmenu-shadow);
border: 1px solid rgba(0, 0, 0, 0.2);
border: var(--border);
}

.nav .nav_menu{
  width: 100%;
text-align : center;
-webkit-justify-content : center;
        justify-content : center;	
text-decoration : none; 
color: white;
}

.nav .nav_menu .fa-film{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction : column;
          flex-direction : column;		
  font-size: 40px;
}
.nav .nav_menu .fa-film .fas_name{
  font-size: 12px;
}


.nav .nav_menu:hover {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items : center;
          align-items : center;	
  height: 100%;
  border-bottom: 3px solid white;
}

.nav .icon:hover{
  border-bottom: none;
 }

